body {
  background-color: rgb(71, 71, 71);
  width: 100%;
}

.main-content {
  align-items: center;
  display: flex;
  flex-direction: column;
}

header {
  background-color: black;
  color: white;
  font-size: 1.5em;
  padding: 15px 0;
  text-align: center;
  width: 100%;
}

.form-preview {
  align-items: center;
  background-image: url('../images/nebulosa.png');
  background-size: cover;
  border: 1px solid black;
  display: flex;
  justify-content: space-around;
  padding: 50px 0;
  width: 100%;
}

.filters-list-card {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.filters-list-card h2 {
  background-color: black;
  color: white;
  padding: 15px 0;
  text-align: center;
  width: 100%;
}

.list-cards {
  display: flex;
}

@media screen and ( max-width : 800px ) {

  .form-preview {
    display: flex;
    flex-direction: column;
  }
}

@media screen and ( max-width : 360px ) {

  header h1 {
    font-size: 25px;
  }
}
